:root {
  --primary-bg: #000;
  --primary-text: #fff;
  --secondary-bg: #fcd535;
  --secondary-text: #181a20;
  --hover-bg: rgba(299, 299, 299, 0.15);
  --active-bg: red;
  --border-color: rgba(44, 44, 44, 1);
}
/* Basic styles for the CurrentLesson page */
.wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 20px;
  justify-content: center;
  box-sizing: border-box;
  padding-left: 10vw;
  padding-right: 10vw;
}

.wrapper-component {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  flex-grow: 1;
  box-sizing: border-box;
  
}


h2 {
  --text-size-base: 14px;
}

.card {
  background-color: silver;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 250px;
  height: 250px;
  margin: 1rem;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}


.card:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.card h2 {
  font-size: 350%;
  margin: 0;
  color: white;
  -webkit-text-stroke-width: 1.7px;
  -webkit-text-stroke-color: black;
}

.card p {
  font-size: 1rem;
  color: #555;
}

/* Error message styling */
.error-message {
  color: #ff0000;
  font-size: 1.2rem;
  text-align: center;
  margin-top: 20px;
}

.contentList {
  margin-top: -8px;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(1, minmax(0px, 1fr));
}

.listImg {
  height: 55px;
  width: 55px;
  border-radius: 8px;
}

.cardItem {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  gap: 10px;
  padding: 10px;
}

.cardItemInside {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
}

.meta {
  color: red;
  font-size: 12px;
}

.head {
  padding: 0;
  margin: 0;
}

.lesson-page {
  width: 100%;
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-template-rows: auto;
  overflow: hidden;
  position: relative
}

.lesson-page .content {
  grid-column: 2 / 3; /* Main content area */
  overflow: auto; /* Allows scrolling if content overflows */
  padding: 20px; /* Adjust padding as needed */
  height: 100%;
  box-sizing: border-box;
}

.lesson-page .content > div {
  max-width: 100%;
  word-wrap: break-word;
  margin: 0;
}

.lesson-page .content .part {
  page-break-inside: avoid; /* Prevents breaking of parts within the content */
}

/* Styling for .correct and .incorrect */
.correct {
  background-color: lightgreen !important;
}
.incorrect {
  background-color: crimson !important;
}

.warming{
  background-color: silver
}
.mane {
  display: block; /* Default layout */
  width: 100%;
  padding: 16px;
  min-height: 90vh; /* Ensures the element is at least the height of the viewport */
}

/* Apply columns if content is expected to exceed the viewport height */
@media (min-height: 100vh) {
  .mane {
    column-count: 2;
    column-gap: 16px;
  }
}

.mane img{
  padding: 0.3rem;
  margin: 0.5rem;
  max-width: 100%;
  max-height: 80vh;
  object-fit: contain; /* Ensure image fits within column */
  border-radius: 8px; 
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    }

.content {
  /* Ensure the content occupies the full width */
  width: 100%;
}

.navigationL {
  background-color: crimson;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  padding: 8px 16px;
  cursor: pointer;
  height: 100%;
  border-radius: 8px
}
.navigationR {
  display:flex;
  flex-direction: column;
  background-color: var(--secondary-bg);
  justify-content: center;
  align-items: center;
  color: #fff;
  border: none;
  text-align:center;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
}
.material-symbols-outlined{
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.nav-dir{
  text-align:center;
  font-size: 0.8em;
  font-weight: 900;
  color: whitesmoke;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.navigation:disabled {
  background: #cf0a0a;
  cursor: not-allowed;
}

.lesson-page button{
  padding: 0.2rem;
  color: #333;
  border: 2px solid #333;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;

}
.lesson-page button{
  padding: 0.2rem;
  color: #333;
  border: 2px solid #333;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
}
.lesson-page button:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #ff003b;
  transition: all .35s;
}

.lesson-page button:hover{
  color: black;
}
.lesson-page h1{
  padding:0;
  margin: 0;
}
.lesson-page ol, ul{
  margin:0;
  
}
.lesson-page li{
  margin-top: 10px;
  background-color: #FFEEAD;
  border-radius: 8px;
  gap: 1rem;
  padding: 5px;
}
.lesson-page li:nth-child(even) {
  margin-top: 5px;
	background: #baf6da;
}
.current-lesson {
  padding: 20px;
}

.next-lesson {
  cursor: pointer;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px 0;
}

.lesson-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.lesson-content {
  background: #fff;
  color: #000;
  padding: 20px;
  border-radius: 8px;
  max-width: 800px;
  width: 100%;
  max-height: 90%;
  overflow-y: auto;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #f00;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.part {
  margin-bottom: 20px;
}

.part h1 {
  font-size: 24px;
}

.part h2 {
  font-size: 20px;
}

.warming {
  background: #fff3e0;
  border: 1px solid #e0a88a;
  padding: 10px;
  border-radius: 5px;
}


div{
  width: 100%
}
.lesson-page .container {
  max-width: 100%;
  text-align:justify;
  margin: 0 auto;
  padding:15px;
  background:#fff;
  margin: 18px auto;
  border-radius:4px;
  position:relative;
  -moz-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
  -webkit-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   overflow:hidden;
}
.lesson-page mark{
    background: linear-gradient(
    -100deg,
    hsla(56, 100%, 50%, 1),
    hsla(48,92%,75%,.7) 95%,
    hsla(48,92%,75%,.1)
    );
    border-radius: 1em 0;
    padding: .15em;
    line-height: 1;
    margin:0;
    padding:0;
}
.var{
  background-color: none;
  border: 1px solid black;
  border-radius: 8px;
}