@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

h1 {
  text-align: center;
  width: 100%;
}

.courses-grid {
  width: 100%;
  padding: 0.5rem 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  padding-top: 0;
  gap: 1.5rem;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.course-item {
  flex-shrink: 0;
  flex-grow: 0!important;
  flex: 1 1 calc(33.333% - 16px);
  box-sizing: border-box;
  padding: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-container {
  position: relative;
  width: 100%;
}

.course-type {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9em;
  z-index: 1;
}

.course-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.course-item h2 {
  margin: 8px 0 0;
  font-size: 1.2em;
  text-align: center;
}

.course-item:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.course-image:hover {
  transform: scale(1.05);
  transition: 0.3s;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
}

.clsz {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  padding: 0.3rem;
  cursor: pointer;
}
.over-grid{
  width: 800px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 62vh;
  column-gap: 8px; 
  background: white;
  padding: 20px;
  border-radius: 5px;
  position: relative; 
}
.detail-image{
  width: 100%;
  height: auto;
  position:relative;
  border-radius: 8px;
}
.det-heading {
  border-radius: 8px 25px 8px 25px;
  padding: 0.2rem;
  background-color: #FF0080;
  text-align: center
}